@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html{
  min-height:100%;
}

body {
  height:100%;
  margin: 0;
  font-family: 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family:'Courier New',
    monospace;
}

*{
  font-family: 'Montserrat', sans-serif;
 }
 #root{
     height:100%;
 }
 .app{
     height: 100%;
 }
 
.nav{
   background-color:#FE5F55;
   display:flex;
   justify-content:space-between;
   height: 50px;
}

.nav__logo{
    color:#333745;
    font-size: 20px;
    padding: 10px 40px;
    display:flex;
    justify-content:center;
    align-items:center
}

.nav__logo span{
    width:30px
}

.nav__navbar{
    display:flex;
    align-items:center
}

.nav button{
    margin:10px 20px;
    padding: 6px;
    display:flex;
    align-items: center;
    cursor:pointer;
}

.nav button span{
    font-size:15px;
    margin-left: 5px;
}
.user{
    display:flex;
    align-items: center;
    position:relative;
    cursor:pointer;
    margin: 4px 0px;
    padding:15px 5px;
    z-index:1
}

.user::after{
    content: "";
    position:absolute;
    width: 50px;
    height: 120%;
    right: -30px;
    border-radius: 50px;
    z-index: -1
}

.user p{
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    position:relative;
    margin: 0

}

.user p span{
    display: flex;
    width: 5px;
    height:5px;
    border-radius: 100%;
    margin-left: 30px;
    position:absolute;
    right: -10px;
    bottom:0px;
    box-shadow:0px 0px .2px .2px #33d933
}



.user:hover{
    background-color: #bfba99;
}

.user__selected{
    /* background-color:#e3812b; */
    background-color:#bfba99;
}





.room{
    margin-bottom: 20px;
}

.room button {
    border:none;
    background-color:orange;
    color:red;
    height:-webkit-max-content;
    height:-moz-max-content;
    height:max-content;
    width:-webkit-max-content;
    width:-moz-max-content;
    width:max-content
}
.add{
    top:0px;
    left:0px;
    z-index:5;
    position: absolute;
    height:100%;
    width: 100%;
    background:rgba(0, 0, 0, 0.633);
    display:flex;
    align-items: center;
    justify-content:center;
    color:black;

}

.add__container{
    padding:20px 30px;
    background-color: #d6c3c9;
}
.left{
    background-color:#D6D1B1;
    margin:none;
    width: 230px;
    display:flex;
    flex-direction: column;
    color:#272a39;
    height: calc(100vh - 50px);
    overflow-y: scroll;
    overflow-x:hidden
}

.left h2{
    width: 100%;
    margin-bottom:20px;
    border-bottom:1px solid black;
    display:flex;
    align-items: center;
    padding-left: 10px;
    padding-bottom: 20px;
}

.left__rooms button{
    margin-left:10px;
    cursor:pointer;
}

.left__rooms span{
    padding:2px 0px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 15px;
}

#chatBody{
    width: calc(100% - 40px);
    padding: 0 20px;
    margin: auto;
    height: calc(100vh - 200px);
    padding-bottom: 100px;
    overflow-y: scroll;
    overflow-x:hidden
}
.chat__messageIn{
    padding: 2px 4px 2px 2px;
    background-color:rgb(255, 225, 252);
    direction: ltr;
    flex-direction: column;
    justify-content: flex-end;
    color: white;
    padding-right: 10px;
    margin: 5px 0px 5px 0px;
    width: 80%;
    margin-left: auto;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    word-wrap: break-word;
    color: #102542
  }
  
  .chat__messageOut{
    border-top-right-radius: 20px;
    border-bottom-left-radius: 20px;
    border-bottom-right-radius: 20px;
    padding: 2px 2px 2px 2px;
    background-color: #102542;
    color: white;
    padding-left: 10px;
    margin: 5px 0px 5px 0px;
    width: 80%;
    word-wrap: break-word;
    color:rgb(255, 225, 252);
  }
  
  .chat__message{
      -webkit-animation:fade-in .5s ease-in;
              animation:fade-in .5s ease-in;
      box-shadow:2px 2px 10px 0px #8888;
      font-size: 1em;
  }

  @-webkit-keyframes fade-in{
     0%{opacity: .5;}
     100%{opacity: 1;}
  }

  @keyframes fade-in{
     0%{opacity: .5;}
     100%{opacity: 1;}
  }

  .chat__message p{
    display: flex;
    flex-wrap: wrap;
    margin-top:20px
  }
  
  .chat__message small{
    font-weight: 100 !important;
  }

  .chat__messageIn small{
    display: block;
    width: 100%; 
    direction: rtl;
  }
.right{
    background-color:#cfd5bf;
    flex:1 1;
    position:relative;
    z-index:1;
    flex:1 1;
   
}

.right__send{
    background-color:#cfd5bf;
    position: relative;
    width:100%;
    display: flex;
    align-items: center;
    height:30px;
    border-top-right-radius: 5px;
    justify-content: center;
    margin-top: 10px;
}

.right__send input{
    width:70%;
    height:87%;
    border:1px solid black;
    border-bottom-left-radius: 10px;
    padding-left:10px;
    background-color: #E4006617;
}

.right__send button{
    width:20%;
    height:100%;
    border:1px solid black;
    cursor:pointer;
    border-top-right-radius: 10px;
    background-color: #fe5f55;
    color:black;
}
.right__send input:focus{
    outline:none

}
.home{
    height: 100%;
}
.home__body{
    display: flex;
    height:100%;
}


.login{
    background-color:whitesmoke;
    width:100%;
    min-height:100vh;
    display: flex;
    white-space: nowrap;
}

.login__container{
    border:1px solid #ad8b0c;
    width:400px;
    height:500px;
    background-color:#EAC435;
    margin: auto;
    margin-top:80px;
    position: relative;
    display: flex;
    color:#542138;
    box-shadow: 0px 0px 10px 1px #8888;
}

.login__container__form{
    overflow: hidden;
    padding-top: 20px;
    position: relative;
    background-color:#EAC435;
    z-index:2;
}

.login__container  p>span{
    color : #E40066;
    cursor:pointer;
}



.login__container h1{
    display:flex;
    cursor:pointer;
    width:80%;
    height: 50px;
    margin: 10%;
    margin-top: 0px;
    border-bottom: 2px solid;
}

.login__select{
    width:100%;
}

.login__right h1{
    margin-left:auto;
}


#login__bird{
   font-size:70px;
   padding-left: 30px;
   z-index: 10;
   position: absolute;
   top:-50px;
   font-size: 50px;
   width: 100px;
   z-index:0
}

.yellowBird{
    -webkit-animation:yellowbird 2.4s ease-out ;
            animation:yellowbird 2.4s ease-out ;
    color:#EAC435  ;
}

.purpleBird{
    -webkit-animation:purplebird 2.4s ease-out ;
            animation:purplebird 2.4s ease-out ;
    color:#542138
}

@-webkit-keyframes yellowbird{
    0%{color:#542138  }
    30%{ transform: translateX(170px) ;color:#542138 }
    35%{ transform: translateX(175px) translateY(-10px) scale(0.95) ;color:#542138 
       }
    60%{transform: translateX(179px) translateY(60px)  scale(0.94) ;color:#542138
       }
    80%{color:#EAC435  ;transform: translateX(-30px) translateY(60px) scale(0.94)} 
    90%{transform: translateX(-10px) translateY(-20px)  scale(0.95)  }  
}

@keyframes yellowbird{
    0%{color:#542138  }
    30%{ transform: translateX(170px) ;color:#542138 }
    35%{ transform: translateX(175px) translateY(-10px) scale(0.95) ;color:#542138 
       }
    60%{transform: translateX(179px) translateY(60px)  scale(0.94) ;color:#542138
       }
    80%{color:#EAC435  ;transform: translateX(-30px) translateY(60px) scale(0.94)} 
    90%{transform: translateX(-10px) translateY(-20px)  scale(0.95)  }  
}

@-webkit-keyframes purplebird{
    0%{color:#EAC435  ;}
    30%{color:#EAC435  ; transform: translateX(170px)}
    35%{color:#EAC435  ; transform: translateX(175px) translateY(-10px) scale(0.95) 
       }
    60%{color:#EAC435  ; transform: translateX(179px) translateY(60px)  scale(0.94)
       }
    80%{color:#542138 ;transform: translateX(-30px) translateY(60px)} 
    90%{ transform: translateX(-10px) translateY(-20px)}  
}

@keyframes purplebird{
    0%{color:#EAC435  ;}
    30%{color:#EAC435  ; transform: translateX(170px)}
    35%{color:#EAC435  ; transform: translateX(175px) translateY(-10px) scale(0.95) 
       }
    60%{color:#EAC435  ; transform: translateX(179px) translateY(60px)  scale(0.94)
       }
    80%{color:#542138 ;transform: translateX(-30px) translateY(60px)} 
    90%{ transform: translateX(-10px) translateY(-20px)}  
}



.login__image span::before{
    width:100px;
   font-size:50px;

}

.login__container div:not(:first-child){
  width:0%;
  transition:width .6s ease-out
}

.login__right{
    background-color:#542138;
    color:#eac435
}

.login__container__form form{
    overflow: hidden;
    white-space: nowrap;
    display: flex;
    flex-direction: column;
    width: 70%;
    margin:auto;
}

.login__container__form .shown{
    -webkit-animation: show .8s ease-out;
            animation: show .8s ease-out;
    opacity: 1;
}

@-webkit-keyframes show{
    0%{opacity: 0}
    30%{opacity: 0}
    70%{opacity: 1}
}

@keyframes show{
    0%{opacity: 0}
    30%{opacity: 0}
    70%{opacity: 1}
}


.login label{
    margin:5px 
}

.login input{
    margin:5px;
    border:none;
}

.login button{
    width:80px;
    padding:5px;
    margin: auto;
    margin-top: 30px;
    margin-bottom: 40px;
    cursor: pointer;
    background-color:#D6C3C9;
    border-radius:2px;
    border:1px solid rgba(0, 0, 0, 0.36);
}

.login p{
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    margin-left: 30px;
    
} 
